* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

li {
    list-style: none;
}

body {
    background-size: 100% 100%;
    width: 1920px;
    height: 1080px;
    color: #fff;
    font-family: '微软雅黑';
}

.people-analysis {
    width: 1920px;
    height: 1080px;
    background: url(./images/bg.jpg) no-repeat;
    position: relative;
}

.y-axis path,
.y-axis line {
    fill: none;
    stroke: none;
    shape-rendering: optimizeSpeed;
}

.y-axis text {
    font-size: 28px;
    font-family: sans-serif;
    fill: #a4d5ff;
}

.page-title {
    width: 209px;
    height: 73px;
    background: url(./images/page-title.png) no-repeat;
    position: absolute;
    top:0px;
    left: 50%;
    transform: translateX(-50%);
    text-indent: -999999px;
}


/*公用标题*/

.part-title {
    height: 47px;
    line-height: 47px;
    display: inline-block;
    color: #fff;
    font-size: 18px;
    padding: 0 40px 0 60px;
    box-sizing: border-box;
    min-width: 260px;
    background: url(./images/part-title-bg.png) no-repeat;
    background-size: 100% 100%;
    position: absolute;
    top: 25px;
    left: 14px;
    &:before {
        content: "";
        display: inline-block;
        position: absolute;
        top: 50%;
        left: 24px;
        width: 28px;
        height: 25px;
        transform: translateY(-50%);
    }
}
.part-con {
    width:340px;
    height: 280px;
    position: absolute;
    top:60px;
    left:10px;
}

/*折线图*/

.active-line {
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
    animation: dash 3s linear forwards;
}

@keyframes dash {
    to {
        stroke-dashoffset: 0;
    }
}

.genre {
    position: absolute;
    top: 85px;
    left: 20px;
    width: 380px;
    height: 400px;
    background: url(./images/part-bg.png) no-repeat;
    background-size: 100% 100%;
    .part-title {
        &:before {
            background: url(./images/genre-title-icon.png) no-repeat;
            background-size: 100% 100%;
        }
    }
}

.age {
    position: absolute;
    top: 510px;
    left: 20px;
    width: 380px;
    height: 450px;
    background: url(./images/part-bg.png) no-repeat;
    background-size: 100% 100%;
    .part-title {
        &:before {
            background: url(./images/age-title-icon.png) no-repeat;
            background-size: 100% 100%;
        }
    }
    .age-chart {
        position: absolute;
        top: 100px;
        left: 40px;
    }
}

.census {
    position: absolute;
    top: 85px;
    right: 20px;
    width: 382px;
    height: 573px;
    background: url(./images/part-bg.png) no-repeat;
    background-size: 100% 100%;
    .part-title {
        &:before {
            background: url(./images/census-title-icon.png) no-repeat;
            background-size: 100% 100%;
        }
    }
    .census-list {
        display: block;
        width: 100%;
        position: relative;
        top: 90px;
        box-sizing: border-box;
        padding: 0 30px;
        li {
            display: block;
            width: 100%;
            margin-bottom: 16px;
            .list-text {
                display: flex;
                width: 100%;
                justify-content: space-between;
                margin-bottom: 6px;
            }
            .list-name {
                color: #73bfff;
                font-size: 18px;
            }
            .list-value {
                color: #fff;
                font-size: 20px;
            }
            .list-progress {
                width: 100%;
                height: 4px;
                background: #404159;
                border-radius: 4px;
                position: relative;
            }
            .progress-bar {
                display: inline-block;
                position: absolute;
                top: 50%;
                left: 0px;
                height: 33px;
                transform: translateY(-50%);
                background: url(./images/census-progress-bar.png) no-repeat left top;
                background-size: 100% 100%;
            }
        }
    }
}

.trend {
    position: absolute;
    bottom: 20px;
    right: 20px;
    width: 382px;
    height: 420px;
    background: url(./images/part-bg.png) no-repeat;
    background-size: 100% 100%;
    .part-title {
        &:before {
            background: url(./images/trend-title-icon.png) no-repeat;
            background-size: 100% 100%;
        }
    }
    .trend-chart {
        position: absolute;
        width: 350px;
        height: 300px;
        top: 0;
        left: 50%;
        transform: translate(-50%, 90px);
    }
}

.map-wrap {
    position: absolute;
    top: 72px;
    left: 50%;
    transform: translateX(-50%);
    width: 1120px;
    height: 950px;
}

/*四色预警图例*/

.map-legent {
    position: absolute;
    font-size: 14px;
    color: #fff;
    display: inline-block;
    width: 270px;
    height: 130px;
    box-sizing: border-box;
    padding-top: 4px;
    background: #0f1c3c url(./images/warning-legend.png) no-repeat -40px -40px;
    bottom: 0px;
    left: 0px;
    line-height: 31px;
    text-indent: 40px;
}


