
/**
弹出框样式
*/
.model-bg {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.8);
    z-index: 99;
    display: block;
    height: auto;
}

.model-container {
  position: relative;
  width: 100%;
}
.model-body {
    position: relative;
    width: 500px;
    top: 100px;
    left: 50%;
    margin-left: -250px;
    background: #000b4e;
    font-size: 32px;
    background-size: 100% 100%;
    text-align: center;
    line-height: 100px;
    color: #fff;
    border-radius: 5px;
}
/*关闭按钮*/
.close-model{
    position: absolute;
    right: 678px;
    top: 86px;
    width: 46px;
    height: 46px;
    cursor: pointer;
    background: url(./images/close-btn.png) no-repeat;
}



